import React, { useEffect, useState } from 'react'
import ReactJson from 'react18-json-view'
import 'react18-json-view/src/style.css'
import { MessageSource, MessageType } from '../types/messages'
import './DebugApp.css'

/**
 * Data structure for subtitle data from a single tab
 */
interface TabSubtitleData {
  tabId: number
  tabTitle: string
  tabUrl: string
  subtitleData: any | null
  error?: string
}

/**
 * Fetches subtitle data from all YouTube tabs' Content Scripts.
 * Since Background is now stateless, we query each Content Script directly.
 */
const getSubtitleFromContentScripts = async (): Promise<TabSubtitleData[]> => {
  // Query all YouTube tabs
  const tabs = await chrome.tabs.query({ url: '*://*.youtube.com/*' })

  if (tabs.length === 0) {
    return []
  }

  // Send message to each tab's Content Script
  const results = await Promise.all(
    tabs.map(async (tab): Promise<TabSubtitleData> => {
      if (!tab.id) {
        return {
          tabId: -1,
          tabTitle: tab.title || 'Unknown',
          tabUrl: tab.url || '',
          subtitleData: null,
          error: 'Tab ID not available',
        }
      }

      try {
        const response = await chrome.tabs.sendMessage(tab.id, {
          source: MessageSource.POPUP,
          type: MessageType.GET_SUBTITLE_DATA,
          data: {},
          timestamp: Date.now(),
        })

        return {
          tabId: tab.id,
          tabTitle: tab.title || 'Unknown',
          tabUrl: tab.url || '',
          subtitleData: response?.data?.subtitleData || null,
        }
      } catch (error) {
        return {
          tabId: tab.id,
          tabTitle: tab.title || 'Unknown',
          tabUrl: tab.url || '',
          subtitleData: null,
          error: error instanceof Error ? error.message : 'Failed to get data',
        }
      }
    }),
  )

  return results
}

export const DebugApp: React.FC = () => {
  const [tabsData, setTabsData] = useState<TabSubtitleData[]>([])
  const [searchTerm, setSearchTerm] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const fetchData = async () => {
    setLoading(true)
    setError(null)

    try {
      const data = await getSubtitleFromContentScripts()
      setTabsData(data)

      if (data.length === 0) {
        setError('No YouTube tabs found. Please open a YouTube video with subtitles.')
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Unknown error')
      console.error('Failed to fetch subtitle data:', err)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchData()
  }, [])

  // Filter data based on search term
  const filterData = (data: any) => {
    if (!searchTerm || !data) return data

    // If data has segments array, filter subtitle segments
    if (data.segments && Array.isArray(data.segments)) {
      const filtered = {
        ...data,
        segments: data.segments.filter((seg: any) => {
          const content = (seg.nativeContent || '') + (seg.targetContent || '')
          return content.toLowerCase().includes(searchTerm.toLowerCase())
        }),
      }
      return filtered
    }

    return data
  }

  // Count tabs with subtitle data
  const tabsWithData = tabsData.filter((t) => t.subtitleData !== null).length

  return (
    <div className="debug-app">
      <header className="app-header">
        <h1>Moki Debug Page</h1>
        <p className="app-subtitle">Subtitle Data Inspector (Multi-Tab)</p>
      </header>

      <div className="debug-controls">
        <input
          type="text"
          placeholder="Search subtitles..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="search-input"
        />
        <button onClick={fetchData} className="refresh-button">
          Refresh
        </button>
      </div>

      {loading && <div className="loading">Loading...</div>}
      {error && <div className="error">{error}</div>}

      {/* Summary */}
      {tabsData.length > 0 && (
        <div className="debug-summary">
          Found {tabsData.length} YouTube tab(s), {tabsWithData} with subtitle data
        </div>
      )}

      <div className="data-sections">
        {tabsData.map((tabData) => (
          <section key={tabData.tabId} className="data-section">
            <h3>
              <span className="tab-badge">Tab {tabData.tabId}</span>
              {tabData.tabTitle}
            </h3>
            <div className="tab-url">{tabData.tabUrl}</div>

            {tabData.error ? (
              <div className="tab-error">{tabData.error}</div>
            ) : tabData.subtitleData ? (
              <ReactJson
                src={filterData(tabData.subtitleData)}
                theme="vscode"
                collapsed={2}
                enableClipboard={true}
                style={{ fontSize: '12px', padding: '10px' }}
              />
            ) : (
              <div className="no-data">
                No subtitle data available
                <small>Make sure subtitles are enabled on this video</small>
              </div>
            )}
          </section>
        ))}

        {tabsData.length === 0 && !loading && !error && (
          <div className="no-data">
            No YouTube tabs found
            <small>Please open a YouTube video to see subtitle data</small>
          </div>
        )}
      </div>
    </div>
  )
}
